.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

*, :before, :after {
  box-sizing: border-box;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
  font-family: inherit;
}

address {
  font-style: normal;
}

a {
  color: currentColor;
  text-decoration: none;
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

body {
  font-family: Inter, sans-serif;
}

.section {
  padding: 100px 30px;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 8px;
  padding-right: 8px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.button {
  padding: var(--wp--preset--spacing--30) var(--wp--preset--spacing--50);
  color: var(--wp--preset--color--foreground);
  background-color: var(--wp--preset--color--button-primary);
  font-size: var(--wp--preset--font-size--small);
  font-weight: 600;
  transition: all .25s cubic-bezier(.785, .135, .15, .86);
  display: block;
}

.button:hover {
  background-color: var(--wp--preset--color--button-secondary);
}

.header {
  flex-direction: column;
  display: flex;
}

.header__first-container {
  background-color: var(--wp--preset--color--secondary);
  font-family: var(--wp--preset--font-family--outfit);
  font-size: var(--wp--preset--font-size--small);
  padding: 10px;
}

.header__first-container .container {
  align-items: center;
  gap: 16px;
  display: flex;
}

.header__contacts-list, .header__social-list {
  align-items: center;
  column-gap: 16px;
  display: flex;
}

.header__time, .header__contacts-item a {
  color: var(--wp--preset--color--white);
  align-items: center;
  gap: 5px;
  display: flex;
}

.header__social-list {
  margin-left: auto;
}

.header__social-item img {
  transition: transform .1s;
}

.header__social-item:hover img {
  transform: scale(1.1);
}

.header__second-container {
  background-color: var(--wp--preset--color--white);
  font-weight: 600;
}

.header__second-container .container {
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.header__title {
  font-family: var(--wp--preset--font-family--outfit);
  font-size: var(--wp--preset--font-size--medium);
  font-style: normal;
  font-weight: 600;
}

.header__title a {
  color: var(--wp--preset--color--foreground);
  background: linear-gradient(to right, currentColor 0%, currentColor 100%) 0 95% / 0 2px no-repeat;
  padding: 0 0 5px;
  transition: all .25s cubic-bezier(.785, .135, .15, .86);
}

.header__title a:hover {
  color: var(--wp--preset--color--primary);
  background-size: 100% 2px;
}

.header__nav-list {
  column-gap: 32px;
  display: flex;
}

.header__nav-item {
  font-size: var(--wp--preset--font-size--small);
}

.header__nav-item a {
  color: var(--wp--preset--color--foreground);
  padding: 12px 0;
  display: block;
}

.header__nav-item--dropdown {
  position: relative;
}

.header__nav-item--dropdown a {
  color: var(--wp--preset--color--foreground);
  align-items: center;
  column-gap: 4px;
  padding: 12px 0;
  display: flex;
}

.header__dropdown-list {
  color: #000;
  height: 0;
  opacity: 0;
  visibility: hidden;
  width: 0;
  pointer-events: none;
  max-height: none;
  background-color: #fff;
  flex-direction: column;
  align-items: normal;
  transition: opacity .1s linear;
  display: flex;
  position: absolute;
  top: 100%;
  left: -1px;
  overflow: hidden;
  transform: translateY(20px);
}

.header__dropdown-list a {
  padding: 12px 15px;
}

.header__nav-item--dropdown:hover .header__dropdown-list {
  height: auto;
  min-width: 200px;
  opacity: 1;
  visibility: visible;
  width: auto;
  pointer-events: auto;
  transition: transform .4s;
  overflow: visible;
  transform: translateY(10px);
}

.hero {
  background-color: var(--wp--preset--color--secondary);
  font-family: var(--wp--preset--font-family--outfit);
}

.hero__container {
  height: 700px;
  background-image: url("hero-img.eac7bfb7.png");
  background-position: 100% 100%;
  background-repeat: no-repeat;
  background-size: 43%;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  display: flex;
}

.hero__text {
  color: var(--wp--preset--color--white);
  font-size: 27px;
  font-weight: 600;
  line-height: 1.4;
}

.hero__text--accent {
  color: var(--wp--preset--color--primary);
}

.hero__title {
  color: var(--wp--preset--color--white);
  margin-top: 16px;
  margin-bottom: 16px;
  font-size: 64px;
  font-weight: 600;
  line-height: 1.5;
}

.hero__button--container {
  gap: 20px;
  display: flex;
}

.header__button {
  color: var(--wp--preset--color--white);
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid;
}

.team {
  background-color: var(--wp--preset--color--background-secondary);
}

.team__subtitle {
  padding: 0 var(--wp--preset--spacing--40);
  width: fit-content;
  font-family: var(--wp--preset--font-family--inter);
  font-size: var(--wp--preset--font-size--small);
  text-align: center;
  background-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--background);
  margin: 0 auto;
}

.team__title {
  text-align: center;
  text-transform: capitalize;
  line-height: 1.3;
  font-size: var(--wp--preset--font-size--slider-title);
  margin-top: 10px;
  font-weight: 600;
}

.team__list {
  column-gap: 32px;
  margin-top: 62px;
  display: flex;
}

.team__item {
  flex-direction: column;
  flex-basis: calc(25% - 24px);
  display: flex;
  box-shadow: 0 6px 30px 7px rgba(33, 37, 71, .1);
}

.team__img-wrapper {
  width: 100%;
  overflow: hidden;
}

.team__img-wrapper img {
  transition: transform 1s;
}

.team__img-wrapper:hover img {
  transform: scale(1.1);
}

.team__info-wrapper {
  text-align: center;
  background-color: var(--wp--preset--color--white);
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  padding: 30px 40px;
  display: flex;
}

.team__info-link {
  color: var(--wp--preset--color--foreground);
  font-family: var(--wp--preset--font-family--outfit);
  font-size: var(--wp--custom--typography--font-size--heading-three);
  font-weight: var(--wp--custom--typography--font-weight--semi-bold);
  line-height: var(--wp--custom--typography--line-height--heading-three);
  background: linear-gradient(to right, currentColor 0%, currentColor 100%) 0 95% / 0 2px no-repeat;
  padding-bottom: 5px;
  transition: background-size .25s cubic-bezier(.785, .135, .15, .86);
}

.team__info-link:hover {
  background-size: 100% 2px;
}

.team__info-specialty {
  color: var(--wp--preset--color--body-text);
  font-family: var(--wp--preset--font-family--inter);
  font-size: var(--wp--preset--font-size--normal);
  font-weight: var(--wp--custom--typography--font-weight--normal);
  line-height: var(--wp--custom--typography--line-height--paragraph);
  flex-grow: 1;
  margin-top: 5px;
}

.team__social-list {
  justify-self: flex-end;
  column-gap: 20px;
  margin-top: 15px;
  display: flex;
}

.team__social-link {
  color: #0dceb5;
  display: block;
}

.team__social-link svg {
  --color1: currentColor;
  transition: transform .1s;
}

.team__social-link:hover {
  transform: scale(1.1);
}

.advantages {
  background-color: var(--wp--preset--color--background-secondary);
  padding-top: 80px;
  padding-bottom: 80px;
}

.advantages__list {
  gap: 32px;
  display: flex;
}

.advantages__list--item {
  background-color: var(--wp--preset--color--background);
  text-align: center;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  display: flex;
}

.advantages__list--header {
  color: var(--wp--preset--color--foreground);
  font-size: 20px;
  font-weight: 600;
  line-height: 1.36;
  font-family: var(--wp--preset--font-family--outfit);
  text-transform: capitalize;
  margin-top: 10px;
  position: relative;
}

.advantages__list--header:before {
  content: "";
  width: 100%;
  height: 2px;
  visibility: hidden;
  background-color: #000;
  transition: all .25s cubic-bezier(.785, .135, .15, .86);
  position: absolute;
  bottom: 0;
  left: -50%;
  transform: scaleX(0);
}

.advantages__list--header:hover:before {
  visibility: visible;
  left: 0;
  transform: scaleX(1);
}

.advantages__list--text {
  color: var(--wp--preset--color--body-text);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.67;
  font-family: var(--wp--preset--font-family--inter);
  margin-top: 10px;
}

.advantages__list--footer {
  color: var(--wp--preset--color--foreground);
  font-family: var(--wp--preset--font-family--inter);
  margin-top: 10px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.67;
  position: relative;
}

.advantages__list--footer .underline {
  position: relative;
}

.advantages__list--footer .underline:before {
  content: "";
